<template>
    <div class="shortVideo">
        <div class="shortVideo-banner">
            <div class="banner-bg" :style="{backgroundImage: 'url('+bannerImg+')'}">
                <img :src="bannerImg" alt="">
            </div>
        </div>
        <div class="shortVideo-main">
            <div class="steps-title">
                接单流程
                <!--<div class="order-status" @click="handleSignup">报名</div>-->
                <div class="order-status" v-if="detailsData&&detailsData.order_status===1" @click="handleSignup">报名</div>
            </div>
            <div class="steps-box">
                <div class="steps-subtitle">
                    订单状态
                </div>
                <div class="steps-main">
                    <el-steps :active="2" align-center>
                        <el-step title="接单中" description="这是一段很长很长很长的描述性文字"></el-step>
                        <el-step title="已接单" description="这是一段很长很长很长的描述性文字"></el-step>
                        <el-step title="制作中" description="这是一段很长很长很长的描述性文字"></el-step>
                        <el-step title="已完成" description="这是一段很长很长很长的描述性文字"></el-step>
                    </el-steps>
                </div>
            </div>
            <div class="shortVideo-details">
                <div class="steps-subtitle" v-if="detailsData">
                    {{detailsData.pd_name}}
                </div>
                <div class="details-box">
                    <div class="details-dec">
                        <label for="">发单时间：</label>
                        <span v-if="detailsData">{{detailsData.createTime|toData}}</span>
                    </div>
                    <div class="details-dec">
                        <label for="">订单类型：</label>
                        <span>短视频</span>
                    </div>
                    <div class="details-dec">
                        <label for="">项目类型：</label>
                        <span v-if="detailsData">{{detailsData.pd_typeStr}}</span>
                    </div>
                </div>
                <div class="details-box">
                    <div class="details-dec">
                        <label for="">客户预算：</label>
                        <span v-if="detailsData">{{detailsData.customer_budgetStr}}</span>
                    </div>
                    <div class="details-dec">
                        <label for="">交片时间：</label>
                        <span v-if="detailsData">{{detailsData.dead_apply_time|toData}}</span>
                        <!--<span>2019-02-12</span>-->
                    </div>
                    <div class="details-dec">
                        <label for="">成片时长：</label>
                        <span v-if="detailsData">{{detailsData.video_timeStr}}</span>
                    </div>
                </div>
                <div class="details-box">
                    <div class="details-dec">
                        <label for="">视频画面比列：</label>
                        <span v-if="detailsData">{{detailsData.pic_ratioStr}}</span>
                    </div>
                    <div class="details-dec">
                        <label for="">播放环境：</label>
                        <span v-if="detailsData">{{detailsData.play_environmentStr}}</span>
                    </div>
                    <div class="details-dec">
                        <label for="">物料信息：</label>
                        <span>附件形式（浏览器直接下载）</span>
                    </div>
                </div>
                <div class="details-box">
                    <div class="details-dec">
                        <label for="">受众群体：</label>
                        <span v-if="detailsData">{{detailsData.audience_usersStr}}</span>
                    </div>
                    <div class="details-dec">
                        <label for="">客户目的：</label>
                        <span v-if="detailsData">{{detailsData.customer_purpose}}</span>
                    </div>
                    <div class="details-dec">
                        <label for="">客户倾向风格：</label>
                        <span v-if="detailsData">{{detailsData.customer_hope_styleStr}}</span>
                    </div>
                </div>
                <div class="details-box">
                    <label for="">预计奖金金额：</label>
                    <span v-if="detailsData">{{detailsData.expected_reward_amount}}</span>
                </div>
                <div class="details-line"></div>
                <div class="details-box">
                    <div class="details-max">
                        <label for="">客户公司简述：</label>
                        <span v-if="detailsData">{{detailsData.company_desc}}</span>
                    </div>
                    <div class="details-max">
                        <label for="">备注信息：</label>
                        <span v-if="detailsData">{{detailsData.remark}}</span>
                    </div>
                </div>
                <div class="steps-subtitle">
                    参与流程
                </div>
                <el-steps :active="currentActive" finish-status="success" simple style="margin-top: 20px">
                    <el-step title="接单"></el-step>
                    <el-step title="提交视频，等待验收通过"></el-step>
                    <el-step title="财务审批，打款"></el-step>
                </el-steps>
            </div>
        </div>
    </div>
</template>

<script>
    import Http from '@/axios/http'
    import Api from '@/axios/api'

    export default {
        name: "shortVideo",
        data() {
            return {
                currentActive: 1,
                bannerImg: 'https://cs.xinpianchang.com/uploadfile/creation/2019/02/21/5c6e61c81ff0d.jpg',
                detailsData: null,
                dictionaryData: []
            }
        },
        methods: {
            getDictionary() {
                Http.post(null, Api.publicList).then(res => {
                    let result = res.data
                    if (result && Number(result.status) === 200) {
                        this.dictionaryData = []
                        this.dictionaryData = result.data
                    } else {
                        this.$message.error(result.msg);
                    }
                })
            },
            getDetails() {
                let param = {
                    userToken: localStorage.getItem('token'),
                    orderId: this.$route.query.id
                }
                Http.post(param, Api.orderDetail).then(res => {
                    let result = res.data
                    if (result && Number(result.status) === 200) {
                        this.detailsData = result.data
                        this.bannerImg = result.data.cover_pic
                        this.currentActive = Number(result.data.status) + 1
                    } else {
                        this.$message.error(result.msg);
                    }
                })
            },
            handleSignup() {
                if (!localStorage.getItem('token')) {
                    this.$message.error('请先登录');
                    return false
                }
                let param = {
                    userToken: localStorage.getItem('token'),
                    orderId: this.$route.query.id
                }
                Http.post(param, Api.order).then(res => {
                    let result = res.data
                    if (result && Number(result.status) === 200) {
                        this.$message({
                            message: result.msg,
                            type: 'success'
                        });
                    } else {
                        this.$message.error(result.msg);
                    }
                })
            }
        },
        mounted() {
            this.getDictionary()
            this.getDetails()
        }
    }
</script>

<style lang="less" scoped>
    .shortVideo {
        width: 100%;
        height: auto;
        min-height: 500px;
        overflow: hidden;
        display: block;
        box-sizing: border-box;
        .shortVideo-banner {
            width: 100%;
            height: auto;
            display: block;
            overflow: hidden;
            margin-bottom: 20px;
            .banner-bg {
                width: 100%;
                height: auto;
                display: block;
                overflow: hidden;
                background-size: cover;
                background-repeat: no-repeat;
                background-position: 50%;
                > img {
                    opacity: 0;
                    width: 100%;
                    height: 100%;
                }
            }
        }
        .shortVideo-main {
            width: 1200px;
            height: auto;
            display: block;
            overflow: hidden;
            margin: 0 auto;
            padding: 10px;
            box-sizing: border-box;
            .steps-subtitle {
                width: 100%;
                font-size: 18px;
                color: #333;
                text-align: center;
                padding: 30px 0;
                box-sizing: border-box;
                font-weight: bold;
                display: block;
                overflow: hidden;
                letter-spacing: 2px;
            }
            .steps-title {
                position: relative;
                width: 100%;
                font-size: 24px;
                color: #333;
                text-align: left;
                padding: 0 200px 40px 6px;
                box-sizing: border-box;
                font-weight: bold;
                display: block;
                overflow: hidden;
                letter-spacing: 2px;
                .order-status {
                    position: absolute;
                    top: 0;
                    right: 0;
                    width: 120px;
                    height: 40px;
                    line-height: 40px;
                    font-size: 18px;
                    text-align: center;
                    border-radius: 6px;
                    cursor: pointer;
                    font-weight: normal;
                    color: #8638FD;
                    background: #fff;
                    border: 1px solid #8638FD;
                    vertical-align: middle;
                    &:hover {
                        color: #fff;
                        background: #8638FD;
                        border: 1px solid #8638FD;
                    }
                }
            }
            .shortVideo-details {
                width: 100%;
                height: auto;
                display: block;
                box-sizing: border-box;
                padding: 10px 20px;
                background: #FFFFFF;
                box-shadow: 2px 2px 10px 0 rgba(219, 217, 223, 0.50);
                padding-bottom: 40px;
                .details-box {
                    width: 100%;
                    height: auto;
                    display: block;
                    overflow: hidden;
                    .details-max {
                        width: 100%;
                    }
                    .details-dec {
                        width: 33.33333333%;
                    }
                    .details-dec, .details-max {
                        height: auto;
                        display: block;
                        overflow: hidden;
                        float: left;
                        box-sizing: border-box;
                        padding-left: 16px;
                        padding-right: 16px;
                        > label {
                            /*width: 180px;*/
                            /*height: auto;*/
                            /*display: block;*/
                            /*overflow: hidden;*/
                            /*float: left;*/
                            display: table-cell;
                            padding-bottom: 16px;
                            color: rgba(0, 0, 0, .85);
                            line-height: 20px;
                            white-space: nowrap;
                            font-size: 15px;
                        }
                        > span {
                            display: table-cell;
                            width: 100%;
                            padding-bottom: 16px;
                            color: rgba(0, 0, 0, .65);
                            line-height: 20px;
                            font-size: 15px;
                        }
                    }

                }
                .details-line {
                    display: block;
                    clear: both;
                    width: 100%;
                    min-width: 100%;
                    height: 1px;
                    margin: 24px 0;
                    padding: 0;
                    color: rgba(0, 0, 0, .65);
                    font-size: 14px;
                    font-variant: tabular-nums;
                    line-height: 1.5;
                    list-style: none;
                    font-feature-settings: "tnum";
                    background: #e8e8e8;
                }
            }
            .steps-box {
                width: 100%;
                height: auto;
                display: block;
                /*overflow: hidden;*/
                /*border: 1px solid #8638FD;*/
                box-sizing: border-box;
                /*border-radius: 10px;*/
                padding: 10px 20px;
                background: #FFFFFF;
                box-shadow: 2px 2px 10px 0 rgba(219, 217, 223, 0.50);
                margin-bottom: 30px;

                .steps-main {
                    width: 100%;
                    height: auto;
                    display: block;
                    overflow: hidden;
                    padding: 10px 0 50px 0;
                }
            }
        }

    }
</style>
