<template>
    <div class="app-registered">
        <div class="tabs-box">
            <div class="tabs-form">
                <el-form :model="userInfo" status-icon :rules="rules" ref="ruleForm" label-width="80px">
                    <el-form-item label="手机号：" prop="user_phone">
                        <el-input v-model="userInfo.user_phone">
                        </el-input>
                    </el-form-item>
                    <el-form-item label="验证码：" prop="pic_code">
                        <el-input type="text" v-model="userInfo.pic_code" placeholder="验证码" maxlength="4"
                                  class="input-code"></el-input>
                        <img @click="getPiccode" :src="'data:image/png;base64,'+randomPiccode" title="验证码"
                             class="but-code code-img"/>
                    </el-form-item>
                    <el-form-item label="验证码：" prop="sms_code">
                        <el-input v-model="userInfo.sms_code">
                            <span class="sms_code" slot="append" @click="getCode">{{phoneCodemsg}}</span>
                        </el-input>
                    </el-form-item>
                    <el-form-item label="密码：" prop="password">
                        <el-input type="password" v-model="userInfo.password" placeholder="密码"
                                  autocomplete="off"></el-input>
                    </el-form-item>
                    <div class="login-btn" @click="handleRegistered('ruleForm')">注册</div>
                    <div class="form-item">注册即代表同意<span>《米粒平台用户协议》</span></div>
                </el-form>
            </div>
            <div class="from-footer" @click="handleLogin">已有账号？登录</div>
        </div>
    </div>
</template>

<script>
    import Http from '@/axios/http'
    import Api from '@/axios/api'

    export default {
        name: "registered",
        data() {
            var validatePhone = (rule, value, callback) => {
                let isMob = /^((\+?86)|(\+86))?(13[0123456789][0-9]{8}|15[0123456789][0-9]{8}||16[0123456789][0-9]{8}|17[0123456789][0-9]{8}|18[0123456789][0-9]{8}|14[0123456789][0-9]{8})$/;
                if (!value) {
                    return callback(new Error('手机号码不能为空'));
                } else {
                    if (isMob.test(value)) {
                        callback();
                    } else {
                        return callback(new Error('请输入正确的电话号码'));
                    }
                }
            };
            let validatepicCode = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error('请输入验证码'));
                } else {
                    callback();
                }
            };
            let validatePass = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error('请输入密码'));
                } else {
                    callback();
                }
            };
            return {
                phoneCodemsg: '发送验证码',
                countdown: 60,
                userInfo: {
                    user_phone: '',
                    sms_code: '',
                    pic_code: '',
                    password: ''
                },
                rules: {
                    user_phone: [{
                        validator: validatePhone,
                        message: '手机号码不能为空',
                        trigger: 'blur'
                    }],
                    sms_code: [
                        {
                            message: '请输入验证码',
                            trigger: 'blur'
                        }
                    ],
                    pic_code: [
                        {validator: validatepicCode, trigger: 'blur'}
                    ],
                    password: [
                        {validator: validatePass, trigger: 'blur'}
                    ]
                },
                randomPiccode: ''
            }
        },
        methods: {
            getCode() {
                let self = this
                if (self.countdown !== 60) return false
                if (!this.userInfo.user_phone) {
                    this.$message.error('请输入手机号');
                    return false
                }
                if (!this.userInfo.pic_code) {
                    this.$message.error('请输入验证码');
                    return false
                }
                let params = {
                    user_phone: this.userInfo.user_phone,
                    pic_code: this.userInfo.pic_code
                }
                self.handleCountdown()
                Http.post(params, Api.send_sms).then(res => {
                    let result = res.data
                    if (result && Number(result.status) === 200) {
                        this.$message({
                            message: result.msg,
                            type: 'success'
                        });
                        self.handleCountdown()
                    } else {
                        this.$message.error(result.msg);
                    }
                })
            },
            handleCountdown() {
                let self = this
                if (self.countdown !== 60) return false
                self.handeleCode = !self.handeleCode
                if (!self.handeleCode) return false
                self.phoneCodemsg = self.countdown + 's后重新获取'
                clearInterval(self.codeTimer)
                self.codeTimer = setInterval(() => {
                    self.countdown = self.countdown - 1
                    if (self.countdown > 0) {
                        self.phoneCodemsg = self.countdown + 's后重新获取'
                    } else {
                        self.countdown = 60
                        self.phoneCodemsg = '发送验证码'
                        self.handeleCode = false
                        clearInterval(self.codeTimer)
                    }
                }, 1000)
            },
            getPiccode() {
                Http.post(null, Api.randomPiccode).then(res => {
                    let result = res.data
                    if (result && Number(result.status) === 200) {
                        this.randomPiccode = result.data.info
                    } else {
                        this.$message.error(result.msg);
                    }
                })
            },
            handleSend() {
                if (!this.userInfo.user_phone) {
                    this.$message.error('请输入手机号');
                    return false
                }
                if (!this.userInfo.pic_code) {
                    this.$message.error('请输入验证码');
                    return false
                }
                let params = {
                    user_phone: this.userInfo.user_phone,
                    pic_code: this.userInfo.pic_code
                }

                Http.post(params, Api.send_sms).then(res => {
                    let result = res.data
                    if (result && Number(result.status) === 200) {
                        this.$message({
                            message: result.msg,
                            type: 'success'
                        });
                    } else {
                        this.$message.error(result.msg);
                    }
                })
            },
            handleRegistered(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        Http.post(this.userInfo, Api.signUp).then(res => {
                            let result = res.data
                            if (result && Number(result.status) === 200) {
                                this.$message({
                                    message: result.msg,
                                    type: 'success'
                                });
                                this.handleLogin()
                            } else {
                                this.$message.error(result.msg);
                            }
                        })
                    } else {
                        return false;
                    }
                });
            },
            handleLogin() {
                this.$emit('childrenLogin')
            }
        },
        mounted() {
            this.getPiccode()
        }
    }
</script>

<style lang="less" scoped>
    .app-registered {
        border-radius: 8px;
        color: #fff;
        width: 100%;
        height: auto;
        background: rgba(255, 255, 255, .9);
        margin: 0 auto;
        display: block;
        overflow: hidden;
        .code {
            cursor: pointer;
        }
        .tabs-box {
            width: 100%;
            height: auto;
            display: block;
            overflow: hidden;
            .tabs-form {
                width: 100%;
                height: auto;
                display: block;
                overflow: hidden;
                padding: 30px 30px 10px 30px;
                box-sizing: border-box;
            }
            .login-btn {
                width: 340px;
                height: 40px;
                line-height: 40px;
                font-size: 16px;
                color: #FFFFFF;
                text-align: center;
                background-image: linear-gradient(-135deg, #1909FE 0%, #8B24FC 100%);
                border-radius: 4px;
                cursor: pointer;
                float: right;
            }
            .form-item {
                width: 100%;
                height: auto;
                color: #595959;
                font-size: 14px;
                line-height: 30px;
                display: block;
                overflow: hidden;
                padding: 10px 0;
                box-sizing: border-box;
                text-align: right;
                > span {
                    cursor: pointer;
                    color: #8B24FC;
                }
            }
            .from-footer {
                width: 100%;
                height: 48px;
                line-height: 48px;
                background: #fafafa;
                border-top: 1px solid #f0f0f0;
                zoom: 1;
                box-sizing: border-box;
                color: #9e9e9e;
                text-align: center;
                cursor: pointer;
                &:hover {
                    color: #8B24FC;
                }
            }
        }
        .code-box {
            width: 200px;
            height: 100px;
            display: block;
            overflow: hidden;
        }
        .input-code {
            width: 60%;
            float: left;
        }
        .but-code {
            width: 36%;
            float: right;
        }
        .code-img {
            border: 1px solid #dbdbdb;
            height: 40px;
            position: relative;
            padding: 2px;
            box-sizing: border-box;
            cursor: pointer;
        }
        .sms_code {
            cursor: pointer;
            width: 100%;
            display: block;
        }
        .el-input-group__append {
            cursor: pointer;
        }
    }
</style>
