<template>
    <div class="index">
        <div class="content">
            <div class="index-lf">
                <div class="form-index">
                    <div class="form-title">
                        个人信息
                    </div>
                    <div class="form-box">
                        <el-form :model="userInfo" status-icon :rules="rules" ref="ruleForm" label-width="100px">
                            <el-form-item label="国家/地区：" prop="address">
                                <el-cascader :options="options" v-model="userInfo.address" :change-on-select="true"
                                             :clearable="true" :filterable="true" placeholder="请选择国家/地区"></el-cascader>
                            </el-form-item>
                            <el-form-item label="手机号：" prop="phone">
                                <el-input v-model="userInfo.phone"></el-input>
                            </el-form-item>
                            <el-form-item label="验证码：" prop="code">
                                <el-input v-model="userInfo.code">
                                    <span class="code" slot="append" @click="handleSend">发送验证码</span>
                                </el-input>
                            </el-form-item>
                        </el-form>
                        <div class="btn-box">
                            <div class="form-btn" @click="handleSubmit('ruleForm')">提交</div>
                        </div>
                    </div>
                </div>
                <div class="module-title">新人必看</div>
                <div class="video-box">
                    <img src="../assets/images/bg.png" alt="">
                </div>
            </div>
            <div class="index-rg">
                <div class="ranking-banner">
                    <el-carousel :interval="5000" arrow="always" height="200px">
                        <el-carousel-item v-for="item in 3" :key="item">
                            <div class="banner-img">
                                <img src="https://cs.xinpianchang.com/uploadfile/banner/2019/04/30/5cc7fb9d8f473.jpg"
                                     alt="">
                            </div>
                        </el-carousel-item>
                    </el-carousel>
                </div>
                <div class="ranking-list">
                    <div class="ranking-header">
                        <div :class="currentInde===1?'active':''" @click="handleTab(1)">月收入排行榜</div>
                        <div :class="currentInde===2?'active':''" @click="handleTab(2)">月接单量排行榜</div>
                    </div>
                    <ul class="ranking-box" v-if="currentInde===1">
                        <li v-for="(list,index) in monthRanking" :key="index">
                            <span class="noun">V{{list.rankingNum}}</span>
                            <span class="portrait-box">
                                <img v-if="list.user_head" :src="list.user_head" alt="">
                                <img v-else src="../assets/images/portrait.png" alt="">
                            </span>
                            <span class="name" v-if="list.userName">{{list.userName}}</span>
                            <span class="name" v-else></span>
                            <span>{{list.income}}</span>
                        </li>
                    </ul>
                    <ul class="ranking-box" v-if="currentInde===2">
                        <li v-for="(list,index) in weekRanking" :key="index">
                            <span class="noun">V{{list.rankingNum}}</span>
                            <span class="portrait-box"><img v-if="list.user_head" :src="list.user_head" alt="">
                            <img v-else src="../assets/images/portrait.png" alt=""></span>
                            <span class="name" v-if="list.userName">{{list.userName}}</span>
                            <span class="name" v-else></span>
                            <span>{{list.orderedNum}}</span>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import Http from '@/axios/http'
    import Api from '@/axios/api'
    import Ares from '../assets/js/area.js'

    export default {
        name: 'index',
        data() {
            var validatePhone = (rule, value, callback) => {
                let isMob = /^((\+?86)|(\+86))?(13[0123456789][0-9]{8}|15[0123456789][0-9]{8}||16[0123456789][0-9]{8}|17[0123456789][0-9]{8}|18[0123456789][0-9]{8}|14[0123456789][0-9]{8})$/;
                if (!value) {
                    return callback(new Error('手机号码不能为空'));
                } else {
                    if (isMob.test(value)) {
                        callback();
                    } else {
                        return callback(new Error('请输入正确的电话号码'));
                    }
                }
            };
            let validateCode = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error('请输入验证码'));
                } else {
                    callback();
                }
            };
            return {
                options: Ares,
                currentInde: 1,
                userInfo: {
                    address: [],
                    phone: '',
                    code: ''
                },
                rules: {
                    phone: [{
                        validator: validatePhone,
                        message: '手机号码不能为空',
                        trigger: 'blur'
                    }],
                    code: [{
                        validator: validateCode,
                        message: '请输入验证码',
                        trigger: 'blur'
                    }
                    ],
                    address: [{
                        message: '请选择国家',
                        trigger: 'blur'
                    }],
                },
                monthRanking: [],
                weekRanking: []
            }
        },
        methods: {
            getBanner() {
                Http.post(null, Api.banner).then(res => {
                    let result = res.data
                    window.console.log(1111, result)
                })
            },
            getRanking() {
                let parma = {
                    userToken: localStorage.getItem('token')
                }
                Http.post(parma, Api.ranking).then(res => {
                    let result = res.data
                    let rankingData = result.data.data
                    this.monthRanking = []
                    this.weekRanking = []
                    if (rankingData) {
                        this.weekRanking = [...rankingData.weekRanking]
                        this.monthRanking = [...rankingData.monthRanking]
                    }
                })

            },
            handleTab(index) {
                this.currentInde = index
            },
            handleSend() {
                if (!this.form.phone) {
                    this.$message.error('请输入手机号');
                    return false
                }
            },
            handleSubmit(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        alert('submit!');
                    } else {
                        return false;
                    }
                });
            }
        },
        mounted() {
            this.getBanner()
            this.getRanking()
        }
    }
</script>

<style lang="less" scoped>
    .index {
        width: 100%;
        /*height: 100%;*/
        height: auto;
        min-height: 100%;
        overflow: hidden;
        /*overflow-y: auto;*/
        display: block;
        padding-top: 20px;
        box-sizing: border-box;
        .index-lf, .index-rg {
            /*background: #FFFFFF;*/
            /*box-shadow: 2px 2px 10px 0 #DBD9DF;*/
            padding-bottom: 10px;
        }
        .index-lf {
            width: 750px;
            height: auto;
            display: block;
            float: left;
            margin-right: 40px;
            .form-index {
                width: 100%;
                height: 326px;
                display: block;
                background: #FFFFFF;
                box-shadow: 2px 2px 10px 0 #DBD9DF;
                .form-title {
                    width: 100%;
                    height: 50px;
                    display: block;
                    overflow: hidden;
                    text-align: left;
                    background: #7B52F8;
                    line-height: 50px;
                    font-size: 16px;
                    color: #FFFFFF;
                    padding: 0 20px;
                    box-sizing: border-box;
                }
                .form-box {
                    width: 100%;
                    padding: 20px;
                    box-sizing: border-box;
                    font-size: 16px;
                    color: #000000;
                    .btn-box {
                        width: 100%;
                        height: 50px;
                        .form-btn {
                            width: 120px;
                            height: 50px;
                            line-height: 50px;
                            font-size: 16px;
                            color: #FFFFFF;
                            text-align: center;
                            background-image: linear-gradient(-135deg, #1909FE 0%, #8B24FC 100%);
                            border-radius: 10px;
                            margin: 0 auto;
                            cursor: pointer;
                        }
                    }
                }
            }
            .module-title {
                width: 100%;
                height: auto;
                display: block;
                overflow: hidden;
                font-size: 24px;
                color: #000000;
                text-align: left;
                line-height: 40px;
                margin: 15px 0;
            }
            .video-box {
                position: relative;
                width: 750px;
                height: 370px;
                display: block;
                overflow: hidden;
                > img {
                    position: absolute;
                    left: 0;
                    right: 0;
                    top: 0;
                    bottom: 0;
                    max-width: 100%;
                    max-height: 100%;
                    margin: auto auto;
                    display: block;
                    overflow: hidden;
                }
            }
        }
        .index-rg {
            width: 400px;
            height: auto;
            display: block;
            float: left;
            .ranking-banner {
                width: 400px;
                height: 200px;
                display: block;
                overflow: hidden;
                margin-bottom: 20px;
                .banner-img {
                    position: relative;
                    width: 400px;
                    height: 200px;
                    display: block;
                    overflow: hidden;
                    background: #ccc;
                    > img {
                        position: absolute;
                        left: 0;
                        right: 0;
                        top: 0;
                        bottom: 0;
                        width: 400px;
                        height: 200px;
                        /*max-height: 100%;*/
                        /*max-width: 100%;*/
                        display: block;
                        overflow: hidden;
                        margin: auto auto;
                    }
                }
            }
            .ranking-list {
                width: 100%;
                height: 546px;
                display: block;
                background: #FFFFFF;
                box-shadow: 2px 2px 10px 0 #DBD9DF;
                .ranking-header {
                    width: 100%;
                    height: 56px;
                    display: flex;
                    flex-direction: row;
                    > div {
                        flex: 1;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        background: #E2E2E2;
                        font-size: 18px;
                        color: #000000;
                        cursor: pointer;
                    }
                    > .active {
                        background: #8638FD;
                        color: #FFFFFF;
                    }
                }
                .ranking-box {
                    width: 100%;
                    height: auto;
                    display: block;
                    overflow: hidden;
                    padding: 0 10px;
                    box-sizing: border-box;
                    > li {
                        width: 100%;
                        height: 48px;
                        display: block;
                        overflow: hidden;
                        border-bottom: 1px solid #eeeeee;
                        > span {
                            display: inline-block;
                            float: left;
                            height: 48px;
                            line-height: 48px;
                            font-size: 16px;
                            color: #000000;
                            width: 100px;
                            padding: 0 20px;
                            box-sizing: border-box;
                        }
                        > .noun {
                            width: 60px;
                        }
                        > .name {
                            width: 140px;
                        }
                        > .portrait-box {
                            position: relative;
                            width: 80px;
                            height: 40px;
                            display: block;
                            margin-top: 4px;
                            > img {
                                position: absolute;
                                left: 0;
                                right: 0;
                                top: 0;
                                bottom: 0;
                                max-height: 100%;
                                max-width: 100%;
                                display: block;
                                overflow: hidden;
                                margin: auto auto;
                            }
                        }
                    }
                }
            }
        }
    }
</style>
