<template>
    <div class="app-login">
        <div class="tabs-box">
            <div class="tabs-form">
                <el-form :model="userInfo" status-icon :rules="rules" ref="ruleForm" label-width="80px">
                    <el-form-item label="用户名：" prop="username">
                        <el-input v-model="userInfo.username" placeholder="用户名"></el-input>
                    </el-form-item>
                    <el-form-item label="密码：" prop="password">
                        <el-input type="password" v-model="userInfo.password" placeholder="密码"
                                  autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="验证码：" prop="pic_code">
                        <el-input type="text" v-model="userInfo.pic_code" placeholder="验证码" maxlength="4"
                                  class="input-code"></el-input>
                        <img @click="getPiccode" :src="'data:image/png;base64,'+randomPiccode" title="验证码"
                             class="but-code code-img"/>
                    </el-form-item>
                    <div class="login-btn" @click="handleLogin('ruleForm')">登录</div>
                    <div class="form-item"><span @click="handleForget">忘记密码</span></div>
                </el-form>
            </div>
            <div class="from-footer" @click="handleRegistered">没有账号？注册</div>
        </div>
    </div>
</template>

<script>
    import Http from '@/axios/http'
    import Api from '@/axios/api'

    export default {
        name: "login",
        data() {
            let validateName = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error('请输入用户名'));
                } else {
                    callback();
                }
            };
            let validatepicCode = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error('请输入验证码'));
                } else {
                    callback();
                }
            };
            let validatePass = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error('请输入密码'));
                } else {
                    callback();
                }
            };
            return {
                userInfo: {
                    username: '',
                    pic_code: '',
                    password: ''
                },
                rules: {
                    username: [
                        {validator: validateName, trigger: 'blur'}
                    ],
                    pic_code: [
                        {validator: validatepicCode, trigger: 'blur'}
                    ],
                    password: [
                        {validator: validatePass, trigger: 'blur'}
                    ]
                },
                randomPiccode: ''
            }
        },
        created() {
            localStorage.clear()
        },
        methods: {
            getPiccode() {
                Http.post(null, Api.randomPiccode).then(res => {
                    let result = res.data
                    if (result && Number(result.status) === 200) {
                        this.randomPiccode = result.data.info
                    }
                })
            },
            handleLogin(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        Http.post(this.userInfo, Api.login).then(res => {
                            let result = res.data
                            if (result && Number(result.status) === 200) {
                                this.$message({
                                    message: result.msg,
                                    type: 'success'
                                });
                                localStorage.setItem('token', result.data.userToken)
                                this.getUserinfo(result.data.userToken)
                                this.handleClose()
                            } else {
                                this.$message.error(result.msg);
                            }
                        })
                    } else {
                        return false;
                    }
                });
            },
            getUserinfo(token) {
                let parma = {
                    userToken: token
                }
                Http.post(parma, Api.userInfo).then(res => {
                    let result = res.data
                    if (result && Number(result.status) === 200) {
                        this.$store.dispatch("addUserinfo", {
                            userInfo: result.data
                        });
                    } else {
                        this.$message.error(result.msg);
                    }
                })
            },
            handleClose() {
                this.$emit('childrenClose')
            },
            handleRegistered() {
                this.$emit('childrenRegistered')
            },
            handleForget() {
                this.$emit('childrenForget')
            }
        },
        mounted() {
            this.getPiccode()
        }
    }
</script>

<style lang="less" scoped>
    .app-login {
        border-radius: 8px;
        color: #fff;
        width: 100%;
        height: auto;
        background: rgba(255, 255, 255, .9);
        margin: 0 auto;
        display: block;
        overflow: hidden;
        .tabs-box {
            width: 100%;
            height: auto;
            display: block;
            overflow: hidden;
            .tabs-form {
                width: 100%;
                height: auto;
                display: block;
                overflow: hidden;
                padding: 30px 30px 10px 30px;
                box-sizing: border-box;
            }
            .login-btn {
                width: 340px;
                height: 40px;
                line-height: 40px;
                font-size: 16px;
                color: #FFFFFF;
                text-align: center;
                background-image: linear-gradient(-135deg, #1909FE 0%, #8B24FC 100%);
                border-radius: 4px;
                cursor: pointer;
                float: right;
            }
            .form-item {
                width: 100%;
                height: auto;
                color: #595959;
                font-size: 14px;
                line-height: 30px;
                display: block;
                overflow: hidden;
                padding: 10px 0;
                box-sizing: border-box;
                > span {
                    float: right;
                    cursor: pointer;
                    &:hover {
                        color: #8B24FC;
                    }
                }
            }
            .from-footer {
                width: 100%;
                height: 48px;
                line-height: 48px;
                background: #fafafa;
                border-top: 1px solid #f0f0f0;
                zoom: 1;
                box-sizing: border-box;
                color: #9e9e9e;
                text-align: center;
                cursor: pointer;
                &:hover {
                    color: #8B24FC;
                }
            }
        }
        .code-box {
            width: 200px;
            height: 100px;
            display: block;
            overflow: hidden;
        }
        .input-code {
            width: 60%;
            float: left;
        }
        .but-code {
            width: 36%;
            float: right;
        }
        .code-img {
            border: 1px solid #dbdbdb;
            height: 40px;
            position: relative;
            padding: 2px;
            box-sizing: border-box;
            cursor: pointer;
        }
    }
</style>
