<template>
    <div class="certification">
        <div class="certification-head">个人认证
            <span>成为米粒认证的影视媒体行业创作人</span>
        </div>
        <el-form ref="userForm" :model="userForm" :rules="rulesuserForm" label-width="200px">
            <el-form-item label="姓名：" prop="user_realname">
                <el-input v-model="userForm.user_realname"></el-input>
            </el-form-item>
            <el-form-item label="头像：">
                <el-upload v-if="!userForm.user_head"
                           class="upload-demo"
                           :action="uploadUrl"
                           :data="additional"
                           :show-file-list="false"
                           :on-success="handleHeadImgSuccess"
                           :before-upload="beforeHeadUpload">
                    <el-button size="small" type="primary">点击上传</el-button>
                </el-upload>
                <div class="card-box" v-if="userForm.user_head">
                    <img :src="userForm.user_head" alt="">
                </div>
            </el-form-item>
            <!--<el-form-item label="手机号：" prop="user_phone">-->
            <!--<el-input v-model="userForm.user_phone"></el-input>-->
            <!--</el-form-item>-->
            <el-form-item label="身份证号：" prop="ID">
                <el-input v-model="userForm.ID" placeholder="请输入身份证号"></el-input>
            </el-form-item>
            <el-form-item label="职业类型：" prop="workingidentify">
                <el-select v-model="userForm.workingidentify" placeholder="请选择职业类型">
                    <el-option v-for="(item,index) in typeList" :key="index" :label="item.name"
                               :value="item.value"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="工作年限：" prop="workingAge">
                <el-select v-model="userForm.workingAge" placeholder="请选择工作年限">
                    <el-option v-for="(item,index) in workList" :key="index" :label="item.name"
                               :value="item.value"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="电子邮箱：" prop="user_email">
                <el-input v-model="userForm.user_email"></el-input>
            </el-form-item>
            <el-form-item label="个人简述：" prop="self_desc">
                <el-input type="textarea" :rows="3" placeholder="个人简述" v-model="userForm.self_desc">
                </el-input>
            </el-form-item>
            <el-form-item label="本人手持身份证照片：">
                <el-upload v-if="!userForm.user_ID_photo"
                           class="upload-demo"
                           :action="uploadUrl"
                           :data="additional"
                           :show-file-list="false"
                           :on-success="handlePositiveSuccess"
                           :before-upload="beforePositiveUpload">
                    <el-button size="small" type="primary">点击上传</el-button>
                </el-upload>
                <div class="card-box" v-if="userForm.user_ID_photo">
                    <img :src="userForm.user_ID_photo" alt="">
                </div>
            </el-form-item>
            <!--<el-form-item label="本人手持身份证反面照片：">-->
            <!--<el-upload v-if="!reverseImg"-->
            <!--class="upload-demo"-->
            <!--:action="uploadUrl"-->
            <!--:data="additional"-->
            <!--:show-file-list="false"-->
            <!--:on-success="handleUploadSuccess"-->
            <!--:before-upload="beforeUpload">-->
            <!--<el-button size="small" type="primary">点击上传</el-button>-->
            <!--</el-upload>-->
            <!--<div class="card-box" v-if="reverseImg">-->
            <!--<img :src="reverseImg" alt="">-->
            <!--</div>-->
            <!--</el-form-item>-->
            <!--<el-form-item label="个人证书照片(选填)：">-->
            <!--<el-upload v-if="!certificate"-->
            <!--class="upload-demo"-->
            <!--:action="uploadUrl"-->
            <!--:data="additional"-->
            <!--:show-file-list="false"-->
            <!--:on-success="handleCertificateSuccess">-->
            <!--<el-button size="small" type="primary">点击上传</el-button>-->
            <!--</el-upload>-->
            <!--<div class="card-box" v-if="certificate">-->
            <!--<img :src="certificate" alt="">-->
            <!--</div>-->
            <!--</el-form-item>-->
            <el-form-item>
                <div class="form-btn" @click="onSubmit('userForm')">提交</div>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    import {
        mapState
    } from "vuex";
    import Http from '@/axios/http'
    import Api from '@/axios/api'

    export default {
        name: "certification",
        data() {
            // var validatePhone = (rule, value, callback) => {
            //     let isMob = /^((\+?86)|(\+86))?(13[0123456789][0-9]{8}|15[0123456789][0-9]{8}||16[0123456789][0-9]{8}|17[0123456789][0-9]{8}|18[0123456789][0-9]{8}|14[0123456789][0-9]{8})$/;
            //     if (!value) {
            //         return callback(new Error('手机号码不能为空'));
            //     } else {
            //         if (isMob.test(value)) {
            //             callback();
            //         } else {
            //             return callback(new Error('请输入正确的电话号码'));
            //         }
            //     }
            // };
            var validateEmail = (rule, value, callback) => {
                let isEmail = /^([a-z0-9]*[-_.]?[a-z0-9]+)*@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/;
                if (value) {
                    if (isEmail.test(value)) {
                        callback();
                    } else {
                        return callback(new Error('请输入正确的邮箱'));
                    }
                } else {
                    callback();
                }
            };
            var isCardNo = (rule, value, callback) => {
                var cardNo = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/;
                if (!value) {
                    return callback(new Error('请输入身份证号'));
                } else {
                    if (!cardNo.test(value)) {
                        return callback(new Error('您输入的身份证号码有误'));
                    } else {
                        callback();
                    }
                }
            };
            return {
                uploadUrl: Api.upload,
                additional: {
                    userToken: localStorage.getItem('token') || ''
                },
                reverseImg: '',
                positiveImg: '',
                certificate: '',
                headImg: '',
                isActive: true,
                fileList: [],
                userForm: {
                    user_realname: '',
                    ID: '',
                    workingidentify: '',
                    user_head: '',
                    self_desc: '',
                    user_ID_photo: '',
                    workingAge: '',
                    user_email: ''
                },
                rulesuserForm: {
                    // user_phone: [{
                    //     required: true,
                    //     validator: validatePhone,
                    //     message: '手机号码不能为空',
                    //     trigger: 'blur'
                    // }],
                    user_realname: [{
                        required: true,
                        message: '请输入姓名',
                        trigger: 'blur'
                    },
                        {
                            min: 0,
                            max: 10,
                            message: '长度在 1 到 10 个字符',
                            trigger: 'blur'
                        }
                    ],
                    user_email: [{
                        validator: validateEmail,
                        trigger: 'blur'
                    }],
                    ID: [{
                        required: true,
                        validator: isCardNo,
                        trigger: 'blur'
                    }],
                    workingidentify: [{required: true, message: '请选择职业类型', trigger: 'change'}],
                    workingAge: [{required: true, message: '请选择工作年限', trigger: 'change'}],
                    self_desc: [{required: true, message: '请填写个人描述', trigger: 'blur'}]
                },
                typeList: [],
                workList: []
            }
        },
        computed: mapState({
            userInfo: state => state.user.userInfo
        }),
        methods: {
            getpublicList() {
                Http.post(null, Api.publicList).then(res => {
                    let result = res.data
                    this.typeList = result.data[7].lists
                    this.workList = result.data[8].lists
                })
            },
            handleHeadImgSuccess(response) {
                if (response.status === 200) {
                    this.userForm.user_head = response.data.info
                } else {
                    this.$message.error(response.msg);
                }
            },
            beforeHeadUpload(file) {
                const extensionName = file.name.substring(file.name.lastIndexOf('.') + 1);
                const isJPG = (extensionName === 'jpg' || extensionName === 'png');
                if (!isJPG) {
                    this.isActive = true;
                    this.$message.error('上传图片只能是 JPG和png 格式!');
                } else {
                    this.isActive = false;
                }
            },
            // handleCertificateSuccess(response) {
            //     if (response.status === 200) {
            //         this.certificate = response.data.data
            //     } else {
            //         this.$message.error(response.msg);
            //     }
            // },
            handlePositiveSuccess(response) {
                if (response.status === 200) {
                    this.userForm.user_ID_photo = response.data.info
                } else {
                    this.$message.error(response.msg);
                }
            },
            beforePositiveUpload(file) {
                const extensionName = file.name.substring(file.name.lastIndexOf('.') + 1);
                const isJPG = (extensionName === 'jpg' || extensionName === 'png');
                if (!isJPG) {
                    this.isActive = true;
                    this.$message.error('上传图片只能是 JPG和png 格式!');
                } else {
                    this.isActive = false;
                }
            },
            // handleUploadSuccess(response) {
            //     if (response.status === 200) {
            //         this.reverseImg = response.data.data
            //     } else {
            //         this.$message.error(response.msg);
            //     }
            // },
            // beforeUpload(file) {
            //     const extensionName = file.name.substring(file.name.lastIndexOf('.') + 1);
            //     const isJPG = (extensionName === 'jpg' || extensionName === 'png');
            //     if (!isJPG) {
            //         this.isActive = true;
            //         this.$message.error('上传图片只能是 JPG和png 格式!');
            //     } else {
            //         this.isActive = false;
            //     }
            // },
            onSubmit(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        if (!localStorage.getItem('token')) {
                            this.$message.error('请先登录');
                            return false
                        }
                        if (!this.userForm.user_head) {
                            this.$message.error('请上头像');
                            return false
                        }
                        if (!this.userForm.user_ID_photo) {
                            this.$message.error('请上传身份证正面照');
                            return false
                        }
                        let pramas = Object.assign(this.userForm, {})
                        pramas.userToken = localStorage.getItem('token')
                        Http.post(pramas, Api.userVerify).then(res => {
                            let result = res.data
                            this.getUserinfo()
                            if (result && Number(result.status) === 200) {
                                this.$message({
                                    message: result.msg,
                                    type: 'success'
                                });
                            } else {
                                this.$message.error(result.msg);
                            }
                        })
                    } else {
                        return false;
                    }
                });
            },
            getUserinfo() {
                let currentToken = localStorage.getItem('token') || '';
                let parma = {
                    userToken: currentToken
                }
                if (currentToken) {
                    Http.post(parma, Api.userInfo).then(res => {
                        let result = res.data
                        if (result && Number(result.status) === 200) {
                            this.$store.dispatch('addUserinfo', {
                                userInfo: result.data
                            });
                        } else {
                            this.$message.error(result.msg);
                        }
                    })
                }
            }
        },
        mounted() {
            this.getpublicList()
        }
    }
</script>

<style lang="less" scoped>
    .certification {
        width: 100%;
        height: auto;
        display: block;
        overflow: hidden;
        margin: 0 auto;
        padding: 20px;
        box-sizing: border-box;
        .certification-head {
            border-bottom: 2px solid #f0f0f0;
            min-height: 48px;
            padding: 0 24px;
            color: #2b2b2b;
            font-weight: 500;
            font-size: 16px;
            background: transparent;
            border-bottom: 1px solid #f0f0f0;
            border-radius: 4px 4px 0 0;
            zoom: 1;
            margin-bottom: 20px;
            box-sizing: border-box;
            span {
                font-size: 12px;
                font-weight: 400;
                margin-left: 8px;
                color: #9e9e9e;
            }
        }
        .form-btn {
            width: 120px;
            height: 50px;
            line-height: 50px;
            font-size: 16px;
            color: #FFFFFF;
            text-align: center;
            background-image: linear-gradient(-135deg, #1909FE 0%, #8B24FC 100%);
            border-radius: 10px;
            cursor: pointer;
            margin-left: 80px;
            margin-top: 20px;
        }
        .card-box {
            width: 300px;
            height: 200px;
            display: block;
            overflow: hidden;
            position: relative;
            > img {
                position: absolute;
                left: 0;
                right: 0;
                top: 0;
                bottom: 0;
                margin: auto auto;
                max-width: 100%;
                max-height: 100%;
                display: block;
                overflow: hidden;
            }
        }
    }
</style>
