import Vue from 'vue'
import Router from 'vue-router'
import List from './views/list.vue'
import Index from './views/index.vue'
import Shortvideo from './views/shortVideo.vue'
import Commercialvideo from './views/commercialVideo.vue'
import Personal from './views/personal'
import Order from './views/personal/order.vue'
import Income from './views/personal/income.vue'
import Integral from './views/personal/integral.vue'
import Certification from './views/personal/certification.vue'
import Works from './views/personal/works.vue'
import Pay from './views/personal/pay.vue'

Vue.use(Router)

export default new Router({
    // mode: 'history',
    base: process.env.BASE_URL,
    routes: [
        {
            path: '/',
            name: 'index',
            component: Index
        }, {
            path: '/list',
            name: 'list',
            component: List
        }, {
            path: '/shortVideo',
            name: 'Shortvideo',
            component: Shortvideo
        }, {
            path: '/commercialVideo',
            name: 'Commercialvideo',
            component: Commercialvideo
        }, {
            path: '/personal',
            name: 'personal',
            component: Personal,
            children: [
                {
                    path: '/',
                    name: 'personal',
                    component: Order
                }, {
                    path: '/income',
                    name: 'income',
                    component: Income
                }, {
                    path: '/integral',
                    name: 'integral',
                    component: Integral
                }, {
                    path: '/certification',
                    name: 'certification',
                    component: Certification
                }, {
                    path: '/works',
                    name: 'works',
                    component: Works
                }, {
                    path: '/pay',
                    name: 'pay',
                    component: Pay
                }
            ]
        },
        {
            path: '/about',
            name: 'about',
            // route level code-splitting
            // this generates a separate chunk (about.[hash].js) for this route
            // which is lazy-loaded when the route is visited.
            component: () => import(/* webpackChunkName: "about" */ './views/About.vue')
        }
    ]
})
