<template>
    <div class="header-container clearfix">
        <div class="header-content">
            <div class="logo" @click="hanldeBack">
                <img src="../assets/images/logo.png" alt="logo" width="289px" height="60px">
            </div>
            <ul class="clearfix nav">
                <li :class="currentRuter===''||currentRuter==='index'?'active':''">
                    <router-link to="/">首页</router-link>
                </li>
                <li :class="currentRuter==='list'?'active':''">
                    <router-link to="/list">订单列表</router-link>
                </li>
            </ul>
            <!-- 用户中心 -->
            <div class="user-operation" v-if="!userInfo">
                <div class="user-login">
                    <a href="javascript:void(0)" @click="handleLogin">
                        登录
                    </a>
                </div>
                <div class="user-line"></div>
                <div class="user-registered">
                    <a href="javascript:void(0)" @click="handleRegistered">
                        注册
                    </a>
                </div>
            </div>
            <div class="user-info clearfix" v-if="userInfo">
                <div class="nickname"><i>欢迎：</i>{{userInfo.user_phone}}</div>
                <div class="user-menu">
                    <ul>
                        <li class="item">
                            <a href="javascript:void(0);" @click="handleJump">个人中心</a>
                        </li>
                        <li class="item">
                            <a href="javascript:void(0);" @click="logout">退出登录</a>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
        <el-dialog
                :title="dialogTitle"
                :visible.sync="dialogVisible"
                width="460px"
                center
                :close-on-click-modal="false"
                :before-close="handleClose">
            <div class="login-box">
                <Login v-if="currentStatus==='login'" @childrenRegistered="handleRegistered"
                       @childrenForget="handleForget" @childrenClose="handleClose"></Login>
                <Registered v-if="currentStatus==='registered'" @childrenLogin="handleLogin"></Registered>
                <Forget v-if="currentStatus==='forget'" @childrenLogin="handleLogin"></Forget>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    import {
        mapState
    } from "vuex";
    import Login from './login'
    import Registered from './registered'
    import Forget from './forget'

    export default {
        name: 'app-header',
        data() {
            return {
                currentRuter: '',
                userNumer: null,
                currentStatus: 'login',
                dialogTitle: '登录',
                dialogVisible: false
            }
        },
        computed: mapState({
            userInfo: state => state.user.userInfo
        }),
        components: {
            Login,
            Registered,
            Forget
        },
        watch: {
            $route(to) {
                this.currentRuter = to.name
            }
        },
        methods: {
            handleLogin() {
                this.dialogTitle = '登录'
                this.currentStatus = 'login'
                this.dialogVisible = true
            },
            handleRegistered() {
                this.dialogTitle = '注册'
                this.currentStatus = 'registered'
                this.dialogVisible = true
            },
            handleForget() {
                this.dialogTitle = '重置密码'
                this.currentStatus = 'forget'
                this.dialogVisible = true
            },
            handleClose() {
                this.dialogTitle = ''
                this.currentStatus = ''
                this.dialogVisible = false
            },
            hanldeBack() {
                this.$router.push({
                    path: '/'
                });
            },
            handleJump() {
                this.$router.push({
                    path: '/personal'
                });
            },
            logout() {
                localStorage.clear();
                this.$store.dispatch("addUserinfo", {
                    userInfo: null
                });
            }
        },
        mounted() {
            this.currentRuter = this.$route.name
        }
    }
</script>

<style lang="less" scoped>
    .header-container {
        height: 80px;
        background: #fff;
        min-width: 600px;
        box-sizing: border-box;
        color: #fff;
        .logo {
            width: 289px;
            height: 60px;
            float: left;
            min-width: 289px;
            cursor: pointer;
            margin-right: 36px;
            margin-left: 0;
            padding-top: 10px;
            box-sizing: border-box;
            img {
                display: block;
            }
        }
        /* nav */
        .nav {
            width: auto;
            height: 80px;
            float: left;
            li {
                height: 80px;
                float: left;
                vertical-align: middle;
                box-sizing: border-box;
                cursor: pointer;
                transition: all .1s;
                padding: 0 18px;
                line-height: 80px;
                box-sizing: border-box;
                font-family: PingFangSC-Medium;
                font-size: 16px;
                color: #000000;
                &:hover {
                    /*border-bottom: 2px solid #fff;
                    height: 58px;*/
                    a {
                        color: #8638FD;
                    }
                }
                a {
                    height: 36px;
                    line-height: 34px;
                    margin-top: 22px;
                    display: block;
                    text-decoration: none;
                    color: #333;
                    letter-spacing: 1px;
                }
            }
            .active {
                /*height: 58px;*/
                a {
                    color: #8638FD;
                    border-bottom: 1px solid #8638FD;
                }
            }
        }
        /* 用户信息 */
        .user-info {
            position: relative;
            width: 200px;
            min-width: 200px;
            height: 80px;
            float: right;
            padding-right: 20px;
            cursor: pointer;
            .nickname {
                font-size: 16px;
                color: #8638FD;
                line-height: 80px;
                &:after {
                    content: "";
                    display: inline-block;
                    margin-left: 10px;
                    width: 0;
                    height: 0;
                    border-left: 6px solid transparent;
                    border-right: 6px solid transparent;
                    border-top: 8px solid #8638FD;
                }
                > i {
                    color: #999;
                    font-size: 14px;
                }
            }
            &:hover {
                .nickname {
                    color: #8638FD;
                    &:after {
                        border-top: 9px solid #8638FD;
                    }
                }
                .user-menu {
                    display: block;
                }
                .nickname:after {
                    transform: rotate(180deg);
                    transition: all 0.5s;
                }
            }
            .user-menu {
                position: absolute;
                top: 62px;
                right: 36px;
                width: 120px;
                z-index: 9999;
                display: none;
                background: #fff;
                color: #666;
                z-index: 99999;
                box-shadow: 0 1px 20px 0 rgba(0, 0, 0, 0.1);
                padding: 8px 0;
                /*border: 1px solid #61ac92;*/
                .item {
                    font-size: 14px;
                    height: 34px;
                    line-height: 34px;
                    text-align: center;
                    &:hover {
                        /*border-bottom: 1px solid #61ac92;*/
                    }
                    a {
                        color: #666;
                        display: block;
                        width: 100%;
                        height: 100%;
                        transition: text-indent 0.3s;
                        outline: 0;
                        &:hover {
                            color: #8638FD;
                        }
                    }
                }
                &:hover {
                    display: block;
                }
            }
        }
        .user-operation {
            min-width: 154px;
            color: #999;
            width: auto;
            float: right;
            font-size: 16px;
            line-height: 80px;
            .user-login,
            .user-registered {
                float: left;
                a {
                    font-size: 16px;
                    color: #5117FD;
                    &:hover {
                        color: #7f57f8;
                    }
                }
            }
            .user-line {
                height: 10px;
                width: 1px;
                background: #999;
                /*opacity: 0.1;*/
                float: left;
                /* box-shadow: 0 0 1px #afb4b8; */
                margin: 35px 15px 0 15px;
            }
        }
        .login-box {
            width: 100%;
            height: auto;
            display: block;
            overflow: hidden;
        }
        .form-btn {
            width: 120px;
            height: 50px;
            line-height: 50px;
            font-size: 16px;
            color: #FFFFFF;
            text-align: center;
            background-image: linear-gradient(-135deg, #1909FE 0%, #8B24FC 100%);
            border-radius: 10px;
            cursor: pointer;
            margin: 0 auto;
        }
    }
</style>
