<script>
    import Http from '@/axios/http'
    import Api from '@/axios/api'
    import marquee from '@/components/marquee'

    export default {
        name: 'home',
        data() {
            return {
                matchList: {
                    totalCount: null, // 总条数
                    page: 1, // 当前显示页号
                    pageSize: 10, // 每页显示数据条数
                    value: []
                },
                currentTab: 1,
                imgArr: ['https://cs.xinpianchang.com/uploadfile/banner/2019/04/30/5cc7fb9d8f473.jpg', 'https://cs.xinpianchang.com/uploadfile/banner/2019/04/29/5cc6db028a93e.jpg', 'https://cs.xinpianchang.com/uploadfile/banner/2019/04/30/5cc7fc635b85d.jpg']
            }
        },
        components: {
            'app-marquee': marquee
        },
        methods: {
            getData() {
                if (!localStorage.getItem('token')) {
                    this.$message.error('请先登录');
                    return false
                }
                let params = {
                    userToken: localStorage.getItem('token'),
                    order_type: this.currentTab,
                    page: this.matchList.page,
                    pageSize: this.matchList.pageSize
                }
                Http.post(params, Api.orderList).then(res => {
                    let result = res.data
                    if (result && Number(result.status) === 200) {
                        this.matchList.value = []
                        this.matchList.value = result.data
                        this.matchList.totalCount = result.total
                    } else {
                        this.$message.error(result.msg);
                    }
                })
            },
            handleTab(index) {
                this.currentTab = index
            },
            handleJumpshortVideo(index) {
                if (this.currentTab === 1) {
                    this.$router.push({
                        path: '/shortVideo',
                        query: {
                            id: index
                        }
                    })
                } else {
                    this.$router.push({
                        path: '/commercialVideo',
                        query: {
                            id: index
                        }
                    })
                }
            }
        },
        mounted() {
            this.getData()
        }
    }
</script>
