<template>
    <div class="pay">
        <div class="pay-box">
            <el-input v-model="input" placeholder="请输入支付宝账号"></el-input>
            <div class="form-btn" @click="handleSumit">提交</div>
        </div>
    </div>
</template>

<script>
    import Http from '@/axios/http'
    import Api from '@/axios/api'

    export default {
        name: "pay",
        data() {
            return {
                input: ''
            }
        },
        methods: {
            handleSumit() {
                if (!this.input) {
                    this.$message.error('请输入支付宝账号');
                    return false
                }
                let param = {
                    userToken: localStorage.getItem('token'),
                    zfb_account: this.input
                }
                Http.post(param, Api.zfbSave).then(res => {
                    let result = res.data
                    if (result && Number(result.status) === 200) {
                        this.$message({
                            message: result.msg,
                            type: 'success'
                        });
                    } else {
                        this.$message.error(result.msg);
                    }
                })
            }
        }
    }
</script>

<style lang="less" scoped>
    .pay {
        width: 100%;
        height: 100%;
        margin: 0 auto;
        display: block;
        overflow: hidden;
        background: #FFFFFF;
        padding-top: 100px;
        box-sizing: border-box;
        .pay-box {
            width: 500px;
            height: auto;
            display: block;
            overflow: hidden;
            margin: 0 auto;
            .form-btn {
                width: 200px;
                height: 50px;
                line-height: 50px;
                font-size: 16px;
                color: #FFFFFF;
                text-align: center;
                background-image: linear-gradient(-135deg, #1909FE 0%, #8B24FC 100%);
                border-radius: 10px;
                cursor: pointer;
                margin: 0 auto;
                margin-top: 30px;
                box-shadow: 0 4px 12px 0px rgba(0, 0, 0, .1);
            }
        }
    }
</style>
