<template>
    <div class="personal">
        <div class="personal-lf">
            <div class="info-box">
                <div class="portrait-box">
                    <div class="portrait-img"></div>
                </div>
                <span class="info-dec" v-if="userInfo">{{userInfo.user_phone}}</span>
                <span class="certification-success" v-if="userInfo&&userInfo.verify_status">已认证</span>
                <span class="certification" v-if="!userInfo||!userInfo.verify_status"
                      @click="handleJump('certification')">未认证</span>
            </div>
            <div class="nav-box">
                <li :class="currentRuter==='personal'?'active':''" @click="handleJump('personal')">我的订单</li>
                <li :class="currentRuter==='income'?'active':''" @click="handleJump('income')">收入记录</li>
                <li :class="currentRuter==='integral'?'active':''" @click="handleJump('integral')">积分记录</li>
                <li :class="currentRuter==='certification'?'active':''" @click="handleJump('certification')">认证信息</li>
                <li :class="currentRuter==='works'?'active':''" @click="handleJump('works')">作品提交记录</li>
                <li :class="currentRuter==='pay'?'active':''" @click="handleJump('pay')">支付宝绑定</li>
            </div>
        </div>
        <div class="personal-rg">
            <div class="rg-main">
                <router-view></router-view>
            </div>
        </div>
    </div>
</template>

<script>
    import {
        mapState
    } from "vuex";

    export default {
        name: "personal",
        data() {
            return {
                currentRuter: 'personal'
            }
        },
        computed: mapState({
            userInfo: state => state.user.userInfo
        }),
        watch: {
            $route(to) {
                this.currentRuter = to.name

            }
        },
        methods: {
            handleJump(index) {
                this.currentRuter = index
                this.$router.push({
                    path: '/' + index
                })
            }
        },
        mounted() {
            this.currentRuter = this.$route.name
        }
    }
</script>

<style lang="less" scoped>
    .personal {
        width: 1200px;
        height: auto;
        display: block;
        overflow: hidden;
        position: relative;
        padding-left: 280px;
        box-sizing: border-box;
        margin: 0 auto;
        .personal-lf {
            position: absolute;
            left: 0;
            top: 20px;
            width: 250px;
            height: 100%;
            min-height: 715px;
            display: block;
            overflow: hidden;
            box-sizing: border-box;
            .info-box {
                width: 250px;
                height: 266px;
                display: block;
                overflow: hidden;
                background: #8638FD;
                box-shadow: 2px 2px 10px 0 rgba(219, 217, 223, 0.50);
                margin-bottom: 24px;
                .portrait-box {
                    width: 100px;
                    height: 100px;
                    display: block;
                    overflow: hidden;
                    margin: 0 auto;
                    box-sizing: border-box;
                    margin-top: 45px;
                    margin-bottom: 20px;
                    .portrait-img {
                        width: 100%;
                        height: 100px;
                        display: block;
                        overflow: hidden;
                        background: #fff;
                        border-radius: 50%;
                    }
                }
                .info-dec {
                    width: 100%;
                    height: auto;
                    line-height: 28px;
                    font-size: 16px;
                    color: #FFFFFF;
                    text-align: center;
                    display: block;
                    overflow: hidden;
                    margin-bottom: 10px;
                }
                .certification-success, .certification {
                    width: 100px;
                    height: 30px;
                    display: block;
                    overflow: hidden;
                    font-size: 14px;
                    line-height: 30px;
                    text-align: center;
                    border-radius: 4px;
                    margin: 0 auto;
                    color: #fff;
                }
                .certification {
                    background: red;
                    cursor: pointer;
                }
                .certification-success {
                    background: #2fda2f
                }
            }
            .nav-box {
                width: 250px;
                height: auto;
                display: block;
                overflow: hidden;
                background: #FFFFFF;
                box-shadow: 2px 2px 10px 0 rgba(219, 217, 223, 0.50);
                > li {
                    width: 230px;
                    height: 70px;
                    display: block;
                    overflow: hidden;
                    text-align: center;
                    font-size: 16px;
                    color: #666;
                    text-align: center;
                    border-bottom: 1px solid #D8D8D8;
                    line-height: 70px;
                    cursor: pointer;
                    margin: 0 auto;
                    &:last-child {
                        border-bottom: none;
                    }
                }
                .active {
                    width: 250px;
                    background: #8638FD;
                    color: #fff;
                }
            }
        }
        .personal-rg {
            width: 100%;
            height: auto;
            min-height: 715px;
            display: block;
            overflow: hidden;
            box-sizing: border-box;
            margin-top: 20px;
            .rg-main {
                width: 100%;
                height: auto;
                min-height: 715px;
                display: block;
                overflow: hidden;
                box-sizing: border-box;
                background: #FFFFFF;
                /*padding: 20px;*/
                /*box-sizing: border-box;*/
            }
        }
    }
</style>
