<script>
    import Http from '@/axios/http'
    import Api from '@/axios/api'

    export default {
        name: "works",
        data() {
            return {
                loading: true,
                listData: []
            }
        },
        methods: {
            getData() {
                if (!localStorage.getItem('token')) {
                    this.$message.error('请先登录');
                    return false
                }
                let parma = {
                    userToken: localStorage.getItem('token')
                }
                Http.post(parma, Api.workRecord).then(res => {
                    let result = res.data
                    this.loading = false
                    if (result && Number(result.status) === 200) {
                        this.listData = result.data
                    } else {
                        this.$message.error(result.msg);
                    }
                })
            },
            handleJumpshortVideo(index) {
                this.$router.push({
                    path: '/shortVideo',
                    query: {
                        id: index
                    }
                })
            }
        },
        mounted() {
            this.getData()
        }
    }
</script>

