import Vue from 'vue'
import Moment from 'moment'

Vue.filter('toData', function (val) {
    return Moment(val).format('YYYY-MM-DD')
})
Vue.filter('toDataDetails', function (val) {
    return Moment(val).format('YYYY-MM-DD HH:mm:ss')
})
Vue.filter('toType', function (val) {
    if (!val) return false
    let currentVal = Number(val)
    let visitsName = ''
    switch (currentVal) {
        case 1:
            visitsName = 'TV广告'
            break
        case 2:
            visitsName = '产品宣传片'
            break
        case 3:
            visitsName = '企业宣传片'
            break
        case 4:
            visitsName = '人物宣传片'
            break
        case 5:
            visitsName = '病毒视频'
            break
        case 6:
            visitsName = '微电影'
            break
        case 7:
            visitsName = '短视频'
            break
        case 8:
            visitsName = 'MG动画'
            break
        case 9:
            visitsName = '创意短片'
            break
        case 10:
            visitsName = '人物访谈'
            break
        case 11:
            visitsName = '活动VCR'
            break
        default:
            visitsName = ''
            break
    }
    return visitsName
})
Vue.filter('toEnvironment', function (val) {
    if (!val) return false
    let currentVal = Number(val)
    let typeName = ''
    switch (currentVal) {
        case 1:
            typeName = '室内'
            break
        case 2:
            typeName = '户外'
            break
        case 3:
            typeName = '海边'
            break
        default:
            typeName = '室内'
            break
    }
    return typeName
})
Vue.filter('audienceUsers', function (val) {
    if (!val) return false
    let currentVal = Number(val)
    let typeName = ''
    switch (currentVal) {
        case 1:
            typeName = '大学生'
            break
        case 2:
            typeName = '公务员'
            break
        default:
            typeName = '大学生'
            break
    }
    return typeName
})
Vue.filter('customerHopestyle', function (val) {
    if (!val) return false
    let currentVal = Number(val)
    let typeName = ''
    switch (currentVal) {
        case 1:
            typeName = '幽默'
            break
        case 2:
            typeName = '严肃'
            break
        default:
            typeName = '严肃'
            break
    }
    return typeName
})
Vue.filter('picRatio', function (val) {
    if (!val) return false
    let currentVal = Number(val)
    let typeName = ''
    switch (currentVal) {
        case 1:
            typeName = '4：3'
            break
        case 2:
            typeName = '16：9'
            break
        default:
            typeName = '4：3'
            break
    }
    return typeName
})
Vue.filter('videoTime', function (val) {
    if (!val) return false
    let currentVal = Number(val)
    let typeName = ''
    switch (currentVal) {
        case 1:
            typeName = '10s'
            break
        case 2:
            typeName = '20s'
            break
        case 3:
            typeName = '30s'
            break
        case 4:
            typeName = '60s'
            break
        default:
            typeName = '10s'
            break
    }
    return typeName
})
Vue.filter('customerBudget', function (val) {
    if (!val) return false
    let currentVal = Number(val)
    let typeName = ''
    switch (currentVal) {
        case 1:
            typeName = '<=5k'
            break
        case 2:
            typeName = '5k~1w'
            break
        case 3:
            typeName = '1w~1.5w'
            break
        case 4:
            typeName = '1.5w~2w'
            break
        case 5:
            typeName = '2w~3w'
            break
        case 6:
            typeName = '3w~5w'
            break
        case 7:
            typeName = '5w~8w'
            break
        default:
            typeName = '5k~1w'
            break
    }
    return typeName
})
