<template>
    <div class="income">
        <el-table
                v-loading="loading" element-loading-text="拼命加载中……"
                :data="listData"
                style="width: 100%"
                border
                header-cell-class-name="myheader"
                @row-click="hanldeDetails">
            <el-table-column
                    prop="id"
                    label="ID"
                    width="80"
                    align="center">
            </el-table-column>
            <el-table-column
                    prop="order_id"
                    label="订单ID"
                    align="center">
            </el-table-column>
            <el-table-column
                    prop="type"
                    label="名称"
                    align="center">
            </el-table-column>
            <!--<el-table-column-->
            <!--prop="type"-->
            <!--label="收入名称"-->
            <!--align="center">-->
            <!--</el-table-column>-->
            <el-table-column
                    prop="payments"
                    label="收入金额"
                    align="center">
            </el-table-column>
            <el-table-column
                    prop="creatTime"
                    label="进账时间"
                    align="center">
                <template slot-scope="scope">{{ scope.row.creatTime |toData}}</template>
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
    import Http from '@/axios/http'
    import Api from '@/axios/api'

    export default {
        name: "income",
        data() {
            return {
                loading: true,
                listData: []
            }
        },
        methods: {
            getData() {
                if (!localStorage.getItem('token')) {
                    this.$message.error('请先登录');
                    return false
                }
                let parma = {
                    userToken: localStorage.getItem('token')
                }
                Http.post(parma, Api.paymentsRecorder).then(res => {
                    let result = res.data
                    this.loading = false
                    if (result && Number(result.status) === 200) {
                        this.listData = result.data
                    } else {
                        this.$message.error(result.msg);
                    }
                })
            },
            hanldeDetails() {
                this.$router.push({
                    path: '/shortVideo',
                    query: {
                        id: 1
                    }
                })
            }
        },
        mounted() {
            this.getData()
        }
    }
</script>

<style lang="less" scoped>
    .income {
        width: 100%;
        height: 100%;
        min-height: 100%;
        display: block;
        overflow: hidden;
        background: #FFFFFF;
        /*padding: 0 20px;*/
        box-sizing: border-box;
        .income-header {
            width: 100%;
            height: 60px;
            display: block;
            overflow: hidden;
            text-align: center;
            line-height: 60px;
            font-size: 16px;
            background: #8638FD;
            color: #FFFFFF;
        }
    }
</style>
