<template>
    <div class="order">
        <div class="pastevents-nav">
            <span :class="currentStatus===''?'active':''" @click="handleCheckStatua('')">进行中订单</span>
            <span :class="currentStatus===1?'active':''" @click="handleCheckStatua(1)">已完成订单</span>
            <span :class="currentStatus===2?'active':''" @click="handleCheckStatua(2)">已结款订单</span>
        </div>
        <ul class="order-list" v-loading="listLoading" element-loading-text="拼命加载中……">
            <div class="list-top">
                <div class="top-lf">订单列表</div>
                <div class="top-middle">订单类型</div>
                <div class="top-middle">状态</div>
                <div class="top-middle">操作</div>
            </div>
            <li v-for="(list,index) in listData" :key="index">
                <div class="order-header">
                    <span>{{list.creattime|toDataDetails}}</span>
                    <span>订单号：{{list.recorder_phone}}</span>
                </div>
                <div class="order-box">
                    <div class="order-lf">
                        <div class="order-img">
                            <img :src="list.cover_pic" alt="">
                        </div>
                        <div class="order-info">
                            <span>{{list.pd_name}}</span>
                        </div>
                    </div>
                    <div class="order-middle"><span>短视频</span></div>
                    <div class="order-middle"><span>报名中</span></div>
                    <div class="order-middle">
                        <span>
                            <div class="min-btn" @click="handleUploadvideo(list.id)">上传视频</div>
                            <div class="min-btn" @click="handleView(list.id)">查看</div>
                        </span>
                    </div>
                </div>
            </li>
        </ul>
        <el-dialog title="视频上传" width="600px" center :visible.sync="dialogFormVisible" :close-on-click-modal="false">
            <div class="dialog-box">
                <el-form label-width="120px" v-loading="loading" element-loading-text="上传中">
                    <el-form-item label="视频类型：" prop="role_type">
                        <el-select v-model="type" placeholder="请选择视频类型">
                            <el-option v-for="(item,index) in typeList" :key="index" :label="item.name"
                                       :value="item.value"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="视频：">
                        <el-upload v-if="!videoUrl"
                                   class="upload-demo"
                                   :action="uploadUrl"
                                   :data="additional"
                                   :show-file-list="false"
                                   :on-success="handlevideoSuccess"
                                   :before-upload="beforevideoUpload">
                            <el-button size="small" type="primary">点击上传</el-button>
                        </el-upload>
                        <div class="card-box" v-if="videoUrl">
                            <video :src="videoUrl"></video>
                        </div>
                    </el-form-item>
                </el-form>
            </div>
            <div slot="footer" class="dialog-footer">
                <el-button type="primary" @click="handleWorksave">保 存</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    import Http from '@/axios/http'
    import Api from '@/axios/api'

    export default {
        name: "order",
        data() {
            return {
                listLoading: true,
                loading: false,
                currentStatus: '',
                listData: [],
                dialogFormVisible: false,
                uploadUrl: Api.upload,
                additional: {
                    userToken: localStorage.getItem('token') || ''
                },
                videoUrl: '',
                type: '',
                isActive: true,
                currentId: '',
                typeList: []
            };
        },
        methods: {
            getData() {
                if (!localStorage.getItem('token')) {
                    this.$message.error('请先登录');
                    return false
                }
                let parma = {
                    userToken: localStorage.getItem('token')
                }
                Http.post(parma, Api.myOrders).then(res => {
                    let result = res.data
                    this.listLoading = false
                    if (result && Number(result.status) === 200) {
                        this.listData = result.data
                    } else {
                        this.$message.error(result.msg);
                    }
                })
            },
            getpublicList() {
                Http.post(null, Api.publicList).then(res => {
                    let result = res.data
                    this.typeList = result.data[0].lists
                })
            },
            handlevideoSuccess(response) {
                this.loading = false
                if (response.status === 200) {
                    this.videoUrl = response.data.info
                } else {
                    this.$message.error(response.msg);
                }
            },
            beforevideoUpload() {
                this.loading = true
                // const tv_id = file.name.substring(file.name.lastIndexOf('.') + 1);
                // if (tv_id != ".mp4" && tv_id != ".rmvb" && tv_id != ".avi" && tv_id != ".ts") {
                //     this.isActive = true;
                //     this.$message.error('只能上传视频文件!');
                // } else {
                //     this.isActive = false;
                // }
            },
            handleCheckStatua(index) {
                this.currentStatus = index
            },
            handleUploadvideo(index) {
                this.currentId = index
                this.dialogFormVisible = true
            },
            handleWorksave() {
                if (!localStorage.getItem('token')) {
                    this.$message.error('请先登录');
                    return false
                }
                if (!this.videoUrl) {
                    this.$message.error('请先上传视频');
                    return false
                }
                window.console.log('视频类型', this.type)
                let parma = {
                    userToken: localStorage.getItem('token'),
                    orderId: this.currentId,
                    url: this.videoUrl
                }
                Http.post(parma, Api.workSave).then(res => {
                    let result = res.data
                    window.console.log(res.data)
                    if (result && Number(result.status) === 200) {
                        this.getData()
                        this.$message({
                            message: result.msg,
                            type: 'success'
                        });
                        this.dialogFormVisible = false
                        this.currentId = ''
                        this.videoUrl = ''
                    } else {
                        this.$message.error(result.msg);
                    }
                })
            },
            handleView(index) {
                this.$router.push({
                    path: '/shortVideo',
                    query: {
                        id: index
                    }
                })
            }
        },
        mounted() {
            this.getData()
            this.getpublicList()
        }
    }
</script>

<style lang="less" scoped>
    .order {
        width: 100%;
        height: auto;
        min-height: 100%;
        display: block;
        overflow: hidden;
        .pastevents-nav {
            width: 100%;
            height: 60px;
            background: #E2E2E2;
            display: block;
            overflow: hidden;
            text-align: center;
            line-height: 60px;
            font-size: 16px;
            color: #333;
            letter-spacing: 1px;
            span {
                width: 33.333%;
                height: 60px;
                cursor: pointer;
                float: left;
                &:hover {
                    color: #8638FD;
                }
            }
            .active {
                color: #fff;
                background: #8638FD;
                &:hover {
                    color: #fff;
                }
            }
        }
        .order-list {
            width: 100%;
            height: auto;
            display: block;
            overflow: hidden;
            padding: 20px;
            box-sizing: border-box;
            .list-top {
                width: 100%;
                height: 40px;
                display: block;
                overflow: hidden;
                margin-bottom: 10px;
                .top-lf, .top-middle {
                    height: 40px;
                    display: block;
                    overflow: hidden;
                    float: left;
                    line-height: 40px;
                    color: #333;
                    font-size: 18px;
                    text-align: center;
                }
                .top-lf {
                    width: 40%;
                    padding-left: 20px;
                    box-sizing: border-box;
                    text-align: left;
                }
                .top-middle {
                    width: 20%;
                }
            }
            > li {
                width: 100%;
                height: 190px;
                border: 1px solid #E2E2E2;
                box-sizing: border-box;
                display: block;
                overflow: hidden;
                margin-bottom: 10px;
                .order-header {
                    width: 100%;
                    height: 50px;
                    line-height: 50px;
                    font-size: 14px;
                    display: block;
                    overflow: hidden;
                    background: #E2E2E2;
                    padding: 0 20px;
                    box-sizing: border-box;
                    > span {
                        padding-right: 30px;
                    }
                }
                .order-box {
                    width: 100%;
                    height: 140px;
                    font-size: 14px;
                    overflow: hidden;
                    padding: 0 20px;
                    box-sizing: border-box;
                    .order-lf, .order-middle {
                        height: 100%;
                        display: block;
                        overflow: hidden;
                        float: left;
                        line-height: 60px;
                        color: #333;
                        font-size: 18px;
                        text-align: left;
                    }
                    .order-lf {
                        width: 40%;
                        padding-right: 20px;
                        box-sizing: border-box;
                        border-right: 1px solid #E2E2E2;
                        position: relative;
                        padding-left: 120px;
                        .order-info {
                            width: 100%;
                            height: 100%;
                            display: table;
                            > span {
                                display: table-cell;
                                vertical-align: middle;
                                font-size: 14px;
                                color: #333;
                            }
                        }
                        .order-img {
                            position: absolute;
                            left: 0;
                            top: 20px;
                            border: 1px solid #ccc;
                            width: 100px;
                            height: 100px;
                            border: 1px solid #ccc;
                            display: block;
                            overflow: hidden;
                            > img {
                                position: absolute;
                                left: 0;
                                right: 0;
                                top: 0;
                                bottom: 0;
                                max-width: 100%;
                                max-height: 100%;
                                display: block;
                                overflow: hidden;
                                margin: auto auto;
                            }
                        }
                    }
                    .order-middle {
                        width: 20%;
                        border-right: 1px solid #E2E2E2;
                        display: table;
                        text-align: center;
                        padding: 0 20px;
                        box-sizing: border-box;
                        &:last-child {
                            border-right: none;
                        }
                        > span {
                            display: table-cell;
                            vertical-align: middle;
                            font-size: 14px;
                        }
                        .view {
                            cursor: pointer;
                            color: #8638FD;
                        }
                    }
                }
            }
        }
        .dialog-box {
            width: 100%;
            height: auto;
            padding: 50px 30px 28px 30px;
            box-sizing: border-box;
            display: block;
            overflow: hidden;
        }
        .card-box {
            width: 400px;
            height: 300px;
            display: block;
            overflow: hidden;
            position: relative;
            > video {
                position: absolute;
                left: 0;
                right: 0;
                top: 0;
                bottom: 0;
                margin: auto auto;
                width: 400px;
                height: 300px;
                display: block;
                overflow: hidden;
            }
        }
        .el-button + .el-button {
            margin-left: 0 !important;
        }
    }
</style>
